body {
  width: 100vw;
  height: 100vh;
  background: white;
  font-family: Montserrat, sans-serif;
}

.outter-container {
  padding: 20px;
  width: 1400px;
  height: 700px;
  background: linear-gradient(to right top, #2b4954, #4e5a44);
  z-index: 1;
}

.inner-container {
  position: absolute;
  width: 1000px;
  height: 600px;
  background: linear-gradient(to right top, #008cb9, #86ba67);
  z-index: 2;
}

body,
.outter-container,
.inner-container,
.size-selector,
.size,
.shoes-footer,
.add-to-cart,
.add-to-wishlist {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}

.nike-watermark,
.fast-watermark {
  position: absolute;
  width: 100px;
  height: 900px;
  font-size: 250px;
  font-weight: 800;
  color: white;
  opacity: 0.2;
}

.nike-watermark {
  transform: rotate(270deg) translate(-300px, -280px);
}

.fast-watermark {
  transform: translate(-450px, 450px);
}

.left-box {
  width: 1000px;
  height: 600px;
}

.nike-image {
  position: absolute;
  width: 650px;
  height: 650px;
  transform: rotate(20deg) translate(-60px, -60px);
  z-index: 4;
}

.share-button {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  height: 80px;
  width: 80px;
  margin: 10px;
  background: black;
  border-radius: 50%;
  transform: translate(450px, 0);
}

.fa-share-alt {
  position: absolute;
  font-size: 2em;
  color: white;
}

.right-box {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  margin: auto;
  flex-direction: column;
  width: 820px;
  height: 620px;
  background: white;
  padding-top: 10px;
  z-index: 3;
}

.top-info,
.shoes-info,
.colors,
.color-circles,
.shoes-size {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 20px;
  color: black;
}

.top-info,
.size-selector {
  flex-direction: row;
}

.shoes-info,
.colors,
.shoes-size,
.shoes-footer {
  flex-direction: column;
}

.color-title,
.size-title {
  color: grey;
  font-size: 20px;
  margin: -35px 50px 0 50px;
}

.size-selector {
  margin: 20px 50px 0 50px;
}

.left-size,
.right-size {
  color: grey;
}

.left-size {
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
}

.right-size {
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
}

.size {
  height: 60px;
  width: 60px;
  border: 1px solid grey;
}

.center-size {
  font-size: 25px;
  color: black;
}

.color {
  width: 40px;
  height: 40px;
  margin-left: 15px;
  border-radius: 50%;
  box-shadow: 3px 3px 7px #000, -7px -7px 7px #000;
}

.shoes-footer {
  width: 400px;
  height: 150px;
  margin: auto;
  padding: 20px;
}

.add-to-cart {
  width: 380px;
  height: 70px;
  background: #009dd1;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  margin: -20px 50px 0 50px;
  color: white;
  font-size: 20px;
  font-weight: 700;
}

.add-to-wishlist {
  width: 380px;
  height: 70px;
  margin: 0 50px 0 45px;
  color: #60c3dc;
  text-decoration: underline;
}

.color-circles {
  margin: -20px 50px 0 25px;
}

.color1 {
  background: #67cde8;
}

.color2 {
  background: #0097ce;
}

.color3 {
  background: #82d894;
}

.color4 {
  background: #edd14c;
}

.color5 {
  background: #6f72d3;
}

.fa-star {
  color: #fec200;
  margin-top: 25px;
}

.star-half {
  color: linear-gradient(to right, #fec200 50%, white 50%);
}

.price {
  display: flex;
  align-self: flex-start;
  margin-left: 150px;
}

.dolar {
  font-size: 2em;
}

.number {
  font-size: 4em;
}

.shoes-model {
  font-size: 3em;
}

.shoes-desc {
  font-size: 23px;
}
